

html,body,#root,.app,.content{
  height: 100%;
  width: 100%;
}
.app{
  display: flex;
  position: relative;
}
::-webkit-scrollbar{
  width: 10px;
}
::-webkit-scrollbar-track{
  background: #e0e0e0;
}
::-webkit-scrollbar-thumb{
  background: #888;
}
/* handle on hover */
::-webkit-scrollbar-track :hover{
  background: #555;
}